.contactDetails{
    text-align: center;
    margin: auto;
    margin-top: 40px;
    width: 90%;
    height: 520px;
    background-color: burlywood;
    -webkit-box-shadow: 1px 0px  3px 2px skyblue;
            box-shadow: 1px 0px  3px 2px skyblue;
    border:1px solid white;
    border-radius: 5px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 10px;
    opacity: 0.9;
    
}

.Input{
    display: block;
    margin-top: 3px;
    border:1px solid red;
    border-radius: 5px;
    width: 450px;
    padding: 10px;
    margin:4px auto
}
@media (min-width:600px){
    .contactDetails{
        width: 500px;
    }
}


@media (max-width:450px){
    .Input{
        width: 150px;
    }
}




